body {
  height: 100vh;
  background-color: #262626;
  justify-content: center;
  align-items: center;
  font-family: Lato, sans-serif;
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
  display: flex;
}

ul li {
  color: #484848;
  letter-spacing: 15px;
  font-size: 80px;
  list-style: none;
  animation: lighting 1.4s linear infinite;
}

@keyframes lighting {
  0% {
    color: #484848;
    text-shadow: none;
  }

  90% {
    color: #484848;
    text-shadow: none;
  }

  100% {
    color: #fff900;
    text-shadow: 0 0 7px #fff900, 0 0 50px #ff6c00;
  }
}

ul li:nth-child(1) {
  animation-delay: 0;
}

ul li:nth-child(2) {
  animation-delay: .1s;
}

ul li:nth-child(3) {
  animation-delay: .2s;
}

ul li:nth-child(4) {
  animation-delay: .3s;
}

ul li:nth-child(5) {
  animation-delay: .4s;
}

ul li:nth-child(6) {
  animation-delay: .5s;
}

ul li:nth-child(7) {
  animation-delay: .6s;
}

ul li:nth-child(8) {
  animation-delay: .7s;
}

